body{
    margin: 0;
}
h1 {
    font-size: large;
}
.app {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: lavenderblush;
}
.container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: mistyrose;
    box-shadow:9px 11px 54px 22px rgba(166,138,166,1) ;
    padding: 2em;

}

.counter{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    /*width: 80%;*/
}
.inputs{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    width: 100%;

}
.buttons{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    width: min-content;
    margin: .5em;
}

input{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    margin: 1em;
    padding: 0 0 0 2em;

}
.negatives{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-flow: wrap;

}


.positives{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-flow: wrap;

}
.button{
    background: lightpink;
    box-shadow:1px 3px 5px 4px rgba(166,138,166,1) ;
    margin: .5em;



}
.number{
    font-size: large;
    background: lightpink;
    padding: .5em;
    margin: .5em;
}
.submit{

    background: pink;
    font-size: medium;
}

.reset{

    background: hotpink;
    font-size: medium;
}


@media (min-width: 1024px){

    .buttons{
       width: 100%;
    }

    .negatives{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: row;

    }


    .positives{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: row;
        flex-flow:row ;

    }



}




